            <div class="commentbox__header">
                <h3><span class="commentbox__header--Comments">Comments</span><span class="commentbox__header--count">{{article.comment_num}}</span></h3>
            </div>
            <!--提问题-->
            <div class="commentBox__question" @click="replyBtn('')">
