<script>
    //"57826e945c21c1dd04b4ad4d"
    import API from "../config.js"
    import {GetArticleById} from "../api/api_article"
    import {GetArticleComments, SendComment} from "../api/api_comment"

    import commentReplyBox from '../components/commentReplyBox.vue'


    module.exports = {
        replace: true,
        data: function () {
            return {
                article: {},
                commentList: [],
                toggle: true,
                selectId: '',
                isLoading: true,
                hasNickName: false,
                username: '',//评论人的昵称
                email: '',//评论人的email
            }
        },
        methods: {
            replyBtn: function (id) {
                if (this.selectId == id) {
                    this.toggle = !this.toggle
                } else {
                    this.toggle = true;
                    this.selectId = id
                }
            },
            submit: function () {

            },
        },
        computed: {},
        ready: function () {
            const scope = this;
            /**
             * 初始化
             * */
            $(window).scrollTop(0);// 滚到顶部
            let articleId = this.$route.params.articleId;


            /**
             * 获取数据
             * */
            //获取文章详情
            GetArticleById(articleId).then(function (data) {
                scope.article = data
                scope.isLoading = false;
            }, function (error) {
                console.log(error)
            });
            //获取文章详情
            GetArticleComments(articleId).then(function (data) {
                scope.commentList = data;
            }, function (error) {
                console.log(error)
            });

            /**
             * 获取游客昵称及邮箱,并设置input显示与否
             * */
            let commentInfo = scope.$localStorage.commentInfo;
            if (!!commentInfo && !!commentInfo.name && !!commentInfo.email) {
                scope.hasNickName = true;
                scope.name = commentInfo.name;
                scope.email = commentInfo.email;
            } else {
                scope.hasNickName = false;
            }


        },
        components: {
            'comment-box': commentReplyBox
        },
        events: {
            'replyThisComment': function (data) {
                let params;
                if (this.hasNickName) {
                    params = {
                        article_id: data.article_id,
                        pre_id: data.pre_id,
                        next_id: [],
                        name: this.name,
                        email: this.email,
                        time: new Date(),
                        content: data.content,
                        state: false,
                        isIReplied: false
                    }

                } else {
                    if (!!data.name && !!data.email) {
                        this.$localStorage.$set({
                            commentInfo:{
                                name: data.name,
                                email: data.email
                            }
                        });

                        this.hasNickName = true;
                        params = {
                            article_id: data.article_id,
                            pre_id: data.pre_id,
                            next_id: [],
                            name: data.name,
                            email: data.email,
                            time: new Date(),
                            content: data.content,
                            state: false,
                            isIReplied: false
                        }
                    } else {
                        alert("请输入昵称和邮箱")
                        return
                    }
                }

                // 发送评论请求,并且向组件发送事件
                this.$broadcast('Submitting');
                SendComment(params).then(()=> {
                    console.log('comment success');
                    this.toggle = !this.toggle;
                    this.$broadcast('SubmitSuccess');
                }, (error)=> {
                    console.log(error)
                    this.$broadcast('SubmitFailure');
                });

            }
        }
    }
</script>


